#app3 {
    border: 1px solid blue;
    width: 50vw;
    height: 50vh;
}

#app3 .square {
    margin-top: 10vw;
    margin-left: 10vw;
    border: 1px solid red;
    width: 10vw;
    height: 10vh;
    transition: transform 1s;
}

#app3 .square.active {
    transform: translateX(15vw);
}